<template>
  <div ref='header' class='header' :style="{'height': `${headerHeight}px`}">
    <img ref="sun" class="sun">
    <img ref="clouds" class="clouds">
    <img ref="mountains" class="mountains">
    <img ref="middleground" class="middleground">
    <img ref="foreground" class="foreground">
    <!-- <div class="figures">
      <img class="zeke"/>
      <img class="blaze"/>
    </div> -->
  </div>
</template>

<script>
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
gsap.registerPlugin(ScrollTrigger)

export default {
  data () {
    return {
      headerHeight: 300
    }
  },
  mounted () {
    this.headerHeight = this.$refs.foreground.clientHeight
    gsap.fromTo(this.$refs.clouds, { x: window.innerHeight }, { x: -100, duration: 200, repeat: true })
    gsap.timeline({
      scrollTrigger: {
        trigger: '#header',
        start: 'top',
        // markers: true,
        scrub: true
      }
    })
      .fromTo(this.$refs.sun, { y: 0 }, { y: 400 }, 0)
      .fromTo(this.$refs.clouds, { y: 0 }, { y: 300 }, 0)
      .fromTo(this.$refs.mountains, { y: 0 }, { y: 200 }, 0)
      .fromTo(this.$refs.middleground, { y: 0 }, { y: 100 }, 0)
  }
}
</script>
