      z-index: 3;
    }
    .middleground {
      @include bros('../../assets/header/middle-ground.png');
      z-index: 4;
    }
    .foreground {
      @include bros('../../assets/header/foreground.png');
      z-index: 5;
      // background-position: bottom;
      // background-size: cover;
    }
    .figures {
      height: 100vh;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      z-index: 6;
      .zeke {
        @include bros('../../assets/header/zeke.png');
        z-index: 7;
      }
      .blaze {
        @include bros('../../assets/header/blaze.png');
